export default {
	data() {
		return {
			title: 'Hello'
		}
	},
	async onLoad() {
		await this.$storage.setItem('info', {
			name: 'jack',
			age: 24
		})
	},
	methods: {
		goDetail() {
			this.$router.push('/pages/detail/detail')
		},
		async getStorage() {
			let ret = await this.$storage.getItem('info')
			console.log(ret)
		},
		async removeStorage() {
			await this.$storage.removeItem('info')
		}
	}
}
